.chart__container,
#cursor {
  width: 800px;
  height: 450px;
  margin: 0 auto;
}

#cursor {
  position: absolute;
}

#line-chart {
  position: relative;
}

svg {
  font: 12px sans-serif;
  text-anchor: middle;
}

rect {
  stroke: lightgray;
  stoke-width: 1px;
  fill: none;
}

.y.axis path {
  fill: none;
  stroke: none;
}

